'use client';

import { useState, useEffect } from 'react';
import { LucideIcon } from 'lucide-react';

interface ClientIconProps {
  icon: LucideIcon;
  className?: string;
  size?: number;
  [key: string]: any;
}

/**
 * ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ Ï†ÑÏö© ÏïÑÏù¥ÏΩò ÎûòÌçº
 * Hydration mismatch Î¨∏Ï†úÎ•º Ìï¥Í≤∞ÌïòÍ∏∞ ÏúÑÌï¥ ÏïÑÏù¥ÏΩòÏùÑ ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ÏóêÏÑúÎßå Î†åÎçîÎßÅ
 */
export default function ClientIcon({ icon: Icon, className, size, ...props }: ClientIconProps) {
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  // ÏÑúÎ≤Ñ ÏÇ¨Ïù¥ÎìúÏóêÏÑúÎäî Îπà divÎ°ú Î†åÎçîÎßÅ
  if (!isClient) {
    return <div className={className} style={{ width: size || 24, height: size || 24 }} />;
  }

  // ÌÅ¥ÎùºÏù¥Ïñ∏Ìä∏ÏóêÏÑúÎäî Ïã§Ï†ú ÏïÑÏù¥ÏΩò Î†åÎçîÎßÅ
  return <Icon className={className} size={size} {...props} />;
}
